:root {
  --background: #fff;
  --foreground: #08090D;
  --button-color: #EBFF3A;
  --font-jetbrains-mono: 'JetBrains Mono', monospace;
  --font-manrope: 'Manrope', sans-serif;
}
@media (prefers-color-scheme: dark) {
  :root {
    --background: #0a0a0a;
    --foreground: #ededed;
  }
}
body {
  background: var(--background);
  color: var(--foreground);
  font-family: var(--font-jetbrains-mono);
  font-size: 16px;
  line-height: 24px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  @media (min-width: 1024px) and (max-width: 1200px) {
    padding-left: 20px !important;
    padding-right: 20px !important;
  }
}
@media (min-width: 1350px) {
  .container {
    max-width: 1320px !important;
  }
}
.tablet-hidden {
  @media (max-width: 1200px) {
    display: none !important;
  }
}
.tablet-show {
  display: none !important;
  @media (max-width: 1200px) {
    display: block !important;
  }
}
.font-jetbrains {
  font-family: var(--font-jetbrains-mono);
}
.font-manrope {
  font-family: var(--font-manrope);
}
.neon-green-bg {
  background-color: #EBFF3A;
}
.top-title {
  font-size: 12px;
  --tw-leading: 12px;
  line-height: 12px;
  --tw-font-weight: var(--font-weight-normal);
  font-weight: var(--font-weight-normal);
  --tw-tracking: 0%;
  letter-spacing: 0%;
  text-transform: uppercase;
  font-family: var(--font-jetbrains-mono);
}
.primary-button {
  background-color: var(--button-color);
  font-family: var(--font-jetbrains-mono);
  display: inline-flex;
  width: auto;
  align-items: center;
  gap: 10px;
  border-radius: 4px;
  padding-inline: calc(var(--spacing) * 6);
  padding-block: 15px;
  text-transform: uppercase;
  transition-property: color, background-color, border-color, outline-color, text-decoration-color, fill, stroke, --tw-gradient-from, --tw-gradient-via, --tw-gradient-to;
  transition-timing-function: var(--tw-ease, var(--default-transition-timing-function));
  transition-duration: var(--tw-duration, var(--default-transition-duration));
  --tw-duration: 200ms;
  transition-duration: 200ms;
  &:hover {
    @media (hover: hover) {
      background-color: var(--color-white);
    }
  }
}
.native-accordion h3 button svg {
  display: none;
}
.native-accordion h3 button .accordion-arrow {
  display: block;
}
@media (max-width: 600px) {
  .testimonial-slider .-ml-4 {
    margin: 0 !important;
  }
  .testimonial-slider .min-w-0.shrink-0.grow-0.basis-full.pl-4.w-full {
    padding-left: 0 !important;
  }
  .banner .primary-button {
    padding: 12px 16px !important;
  }
}
.hide-default-arrow svg {
  display: none;
}
.hide-default-arrow .accordion-arrow {
  display: block !important;
}
